/* Contenitore per logo + scritta */
.headerContainer {
  display: flex !important;
  align-items: center !important;
  justify-content: flex-start !important;
  padding-left: 16px !important;
  gap: 8px !important;
  max-width: 100vw !important;
  box-sizing: border-box !important;
  overflow-x: hidden !important;
}

/* Immagine logo */
.headerContainer img {
  max-width: 40px !important; /* limita dimensione massima */
  height: auto !important;
}

/* Titolo "fitnessapp ai" adattivo con bordo bianco e testo trasparente */
.outlinedTitle {
  font-size: clamp(1.2rem, 3vw, 2rem) !important; /* ridotto e adattivo */
  font-weight: bold !important;
  color: transparent !important;
  -webkit-text-stroke: 1px white !important;
  text-stroke: 1px white !important;
  background-color: transparent !important;
  margin: 0 !important;
  padding: 0 !important;
  user-select: none !important;
  white-space: nowrap !important;
  max-width: 100% !important;
  overflow-wrap: break-word !important;
  text-align: left !important;
}

/* Tablet (≤ 768px) */
@media (max-width: 768px) {
  .headerContainer {
    padding-left: 12px !important;
  }

  .headerContainer img {
    max-width: 32px !important; /* più piccolo */
  }

  .outlinedTitle {
    font-size: 1.5rem !important;
    -webkit-text-stroke-width: 0.8px !important;
  }
}

/* Smartphone (≤ 480px) */
@media (max-width: 480px) {
  .headerContainer {
    padding-left: 8px !important;
    flex-direction: column !important;
    align-items: flex-start !important;
  }

  .headerContainer img {
    max-width: 28px !important; /* ancora più piccolo */
  }

  .outlinedTitle {
    font-size: 1.2rem !important;
    -webkit-text-stroke-width: 0.6px !important;
    text-align: left !important;
    white-space: normal !important;
    word-break: break-word !important;
  }
}
