
#root {
  max-width: 1280px;
  margin: 0 auto;
  padding: 2rem;
  text-align: center;
}
/* Contenitore per logo e titolo */
.header-container {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  padding: 1rem;
  flex-wrap: wrap;
}

/* Logo */
.logo {
  max-width: 100%;
  height: auto;
  width: 6em; /* Impostazione predefinita */
  transition: filter 300ms;
}

.logo:hover {
  filter: drop-shadow(0 0 2em #646cffaa);
}

.logo.react:hover {
  filter: drop-shadow(0 0 2em #61dafbaa);
}

/* Titolo */
.outlinedTitle {
  font-size: 3rem;
  font-weight: bold;
  margin: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  flex-shrink: 1;
}

/* Media Queries per dispositivi con larghezza massima di 768px (tablet) */
@media (max-width: 768px) {
  .logo {
    width: 5em; /* Riduce la dimensione del logo su tablet */
  }

  .outlinedTitle {
    font-size: 2rem; /* Riduce la dimensione del titolo su tablet */
  }
}

/* Media Queries per dispositivi con larghezza massima di 480px (smartphone) */
@media (max-width: 480px) {
  .header-container {
    flex-direction: column;
    align-items: center;
    gap: 6px;
    padding: 0.5rem;
  }

  .logo {
    width: 4em; /* Riduce ulteriormente la dimensione del logo su smartphone */
  }

  .outlinedTitle {
    font-size: 1.5rem; /* Riduce ulteriormente la dimensione del titolo su smartphone */
    text-align: center;
  }
}
